<template>
<div class="main">
      <div class="menu">
        <router-link to="/" class="dot" @mouseover="homeH = true" @mouseleave="homeH = false"><img :src="homeHover"></router-link>  
        <router-link to="/projects/trip-planner" class="dot project"><img src="./assets/trip-planner-img.webp"></router-link>
        <router-link to="/contact" class="dot" @mouseover="contactH = true" @mouseleave="contactH = false"><img :src="contactHover"></router-link>    
        <router-link to="/explorer" class="dot"  @mouseover="hover = true" @mouseleave="hover = false"><img :src="pictureHover"></router-link>
      </div>
    <div class="content"><router-view/></div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data () {
    return {
      home: require("./assets/home.png"),
      homeWhite: require("./assets/home-white.png"),
      pictureStatic: require("./assets/boussole.png"),
      pictureGif: require("./assets/boussole-white.png"),
      contact: require("./assets/contact.png"),
      contactWhite: require("./assets/contact-white.png"),
      hover: false,
      contactH: false,
      homeH: false
    }
  },
  computed: {
    homeHover () {
      if (this.homeH == true) {
        return this.homeWhite
      } else {
        return this.home
      }
    },
    pictureHover () {
      if (this.hover == true) {
        return this.pictureGif
      } else {
        return this.pictureStatic
      }
    },
    contactHover () {
      if (this.contactH == true) {
        return this.contactWhite
      } else {
        return this.contact
      }
    }
  
  }
}
</script>

